.index-main {
  width: 320px;
  margin: 0 auto;
  min-height: 177px;
  background-color: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (min-width: @tablet-width) {
    width: 768px;
    min-height: 226px;
  }

  @media (min-width: @desktop-width) {
    width: 1150px;
    min-height: 255px;
  }
}

.index-main__title {
  font-family: inherit;
  font-style: normal;
  font-weight: bold;
  font-size: 27px;
  line-height: 30px;

  @media (min-width: @tablet-width) {
    font-family: inherit;
    font-weight: bold;
    font-size: 43px;
    line-height: 50px;
    display: flex;
    align-items: center;
    text-align: center;
  }

  @media (min-width: @desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}
